.footerWrapper {
    background-color: #314584;
}

.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
}

.footer span:nth-child(1) {
    color: white;
}

.footer span:nth-child(2) {
    color: #ff3946;
}

.footerNavBar {
    display: flex;
    justify-content: center;
    align-items: center;
}

.footerNavBar ul {
    display: flex;
    list-style-type: none;
    justify-content: center;
    align-items: center;
    padding: 0;

}

.footerNavBar li  a {
    text-decoration: none;
    color: white !important;
    font-size: 10px;
    margin: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
}

.social {
    display: flex;
    justify-content: center;
    align-items: center;
}

.social a{
    margin: 0 45px;
}
