.header {
    display: flex;
    position: relative;
    height: 7vh;
    color: #314584;
}

.logoWrapper {
    display: flex;
    justify-content: flex-start;
    width: 20vw;
    order: 2;
}

.logo {
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 5px;

}


.leftNavBar {
    display: flex;
    width: 20vw;
    position: relative;
    justify-content: flex-start;
    align-items: center;

    order: 1;
}

.menuBtn {
    display: flex;
    position: relative;
    margin-left: 10px;
}

.hiddenContainer {
    display: none;
}

.menuBtn:hover .hiddenContainer {
    display: flex;
}

.rightNavBar {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 60vw;
    order: 3;
}

.rightNavBar div {
    display: flex;
    align-items: center;
    margin-right: 40px;
}


.menuBlock {
    display: flex;
    flex-flow: wrap;
    justify-content: center;
    align-items: center;
}

.leftNavBar li  a {
    text-decoration: none;
    color: #314584 !important;
    padding: 0;
    margin: 0;
}

#language {

}

.leftNavBar ul {
    list-style-type: none;
    display: none;
    padding: 0;
}

.leftNavBar ul li {
    margin-right: 10px;
}

.rightNavBar .button {
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 255, .2);
    border: none;
    outline: none;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ff3946;
    width: 100px;
    height: 44px;
    border-radius: 5px;
    margin-left: 5px;
    margin-bottom: 10px;

}

.button strong:nth-child(1) {
    margin-right: 10px;
}

/*.endBlock {*/
/*    width: 20vw;*/
/*}*/

@media screen and (min-width: 768px){
    .header {
        height: 10vh;
    }
    .logoWrapper {
        width: 20vw;
        order: 1;
        justify-content: center;
    }
    .leftNavBar {
        width: 45vw;
        order: 2;
    }
    .rightNavBar {
        width: 15vw;
        order: 3;
    }
    .menuBtn {
        display: none;
    }
    .leftNavBar ul {
       display: flex;
   }
    .rightNavBar .button {
        width: 150px;
    }
}


