.aboutUs {
    display: flex;
    margin-left: 20vw;
    height: 93vh;
    color: #314584;
}

.title {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 60vw;
    margin-top: 40px;
}

.slogan {
    font-size: 3.0em;
    margin: 10px 0;
}

.text {
    font-size: 0.7em;
    margin: 10px 0;
    color: #7b8185;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.moreAboutUs {
    margin-right: 10px;

}

.moreAboutUsBtn {
    margin-top: 0;
    background-color: #ff3946;
    color: white;
    border-radius: 40px;
    height: 50px;
}

.moreAboutUsBtn:nth-child(2){

}

.illustration {
    display: none;
}

@media screen and (min-width: 768px) {
    .aboutUs {
    }
    .slogan {
    }
    .title {
        width: 30vw;
    }
    .illustration {
        display: flex;
    }

}