.contactWrapper {
    display: flex;
    justify-content: center;
    background-color: violet;
}

.contact {
    background-color: gray;
    width: 60vw;
    height: 55vh;
}

.slogan {
    display: flex;
    flex-direction: column;
}

.slogan span:nth-child(1) {
    color: #ff3946;
    margin: 10px 0;
}

.slogan span:nth-child(2) {
    color: #314584;
    margin: 10px 0;
    font-size: 1.5em;
}

form {
    display: flex;
    flex-wrap: wrap;
}

input {
    width: 100%;
}

button {
    margin-top: 10px;
    margin-right: auto;
    margin-left: auto;
}


@media screen and (min-width: 768px) {
    form {
        display: flex;
        justify-content: space-between;
    }
    input {
        width: 29vw;
    }
    textarea {
        margin-top: 10px;
        width: 60vw;
        height: 10vh;
    }
}