.worksWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.works {
    display: flex;
    width: 60vw;
    flex-direction: column;
}

.worksTitle {
    font-size: 1.5em;
    color: #314584;
}

.worksExamples {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.seeMoreText {
    color: #ff3946;
}

@media screen and (min-width: 768px) {
    .worksWrapper {
        height: 100vh;
    }
}